.muidocs-icon-custom-github:before {
    content: "\e625";
}

.footerComponent {
  color: white;
  padding: .8rem;
  text-align: center;
  font-size: 12px;
  background-image:
    -webkit-gradient(
      linear,
      left top, right top,
      from(#4DD0E1), to(#E0F7FA)
    );
  background-image:
    -webkit-linear-gradient(
      left,
      #4DD0E1, #E0F7FA
    );
  background-image:
    -o-linear-gradient(
      left,
      #4DD0E1, #E0F7FA
    );
  background-image:
    linear-gradient(
      to right,
      #4DD0E1, #E0F7FA
    );
}

.github-button {
  color: #00ACC1;
}



