body, html {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App {
  height: 100%;
  width: 100%;
  background:
   -webkit-repeating-radial-gradient(
     0 0,
    circle,
     white,
    rgba(224, 247, 250, 0.4) 200px
   );
  background:
   -o-repeating-radial-gradient(
     0 0,
    circle,
     white,
    rgba(224, 247, 250, 0.4) 200px
   );
  background:
   repeating-radial-gradient(
     circle at 0 0,
     white,
    rgba(224, 247, 250, 0.4) 200px
   );
}

main {
  padding-bottom: 150px;
}



